
<template>
    <div class="managerDetailPage">
        <el-row :gutter="20" type="flex" align="middle">
            <h2>基金经理详情--{{params.name}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</h2>

            <el-button size="small" type="primary" id="login" @click="exportPdf($event)">生成pdf报告</el-button>
        </el-row>

        <h3>历史风格表</h3>
        <table-plus :data="styles" >
            <el-table-column  prop="year" label="年份" width="100" />
            <el-table-column  prop="valuegrowth" :formatter="styleFormatter" label="价值成长" width="100" />
            <el-table-column  prop="bigsmall" :formatter="styleFormatter" label="大小盘"width="100" />
            <el-table-column  prop="volatility" :formatter="styleFormatter" label="权益暴露" width="100" />
        </table-plus>
        <br>
        <h3>风险收益统计表</h3>
        <table-plus :data="riskProfitStats">
            <el-table-column  prop="year" label="年份" width="100" />
            <el-table-column  prop="cumReturn" :formatter="riskFormatter" label="年度收益" width="100" />
            <el-table-column  prop="aveExcessReturnSza" :formatter="riskFormatter" label="超越基准"width="100" />
            <el-table-column  prop="maxDrawdown" :formatter="riskFormatter" label="最大回撤" width="100" />
            <el-table-column  prop="monthlyWinRatioSza" :formatter="riskFormatter" label="月胜率" width="100" />
            <el-table-column  prop="beta" :formatter="riskFormatter" label="贝塔" width="100" />
            <el-table-column  prop="sharpe0" :formatter="riskFormatter" label="夏普率" width="100" />
            <el-table-column  prop="calmar" :formatter="riskFormatter" label="卡码率" width="100" />
        </table-plus>
        <br>
        <h3>四大能力</h3>
        <table-plus  :data="capabilityExplain"  :show-header="false" >
            <el-table-column  prop="name" :formatter="styleFormatter" label="能力名称" width="100" />
            <el-table-column  prop="value" :formatter="styleFormatter" label="个人能力" width="400" />
        </table-plus >
        <br>
        <h3>交易模式--长期持股特征</h3>
        <table-plus :data="longholdstats">
            <el-table-column  prop="code" :formatter="styleFormatter" label="代码" width="100" />
            <el-table-column  prop="ratio" :formatter="styleFormatter" label="比率" width="200" />
            <el-table-column  prop="description" :formatter="styleFormatter" label="描述" width="200" />
        </table-plus>
        <br>
        <!--h3>历史访谈</h3>
        <div>
           {{interview||'无'}}
        </div-->
    </div>
</template>
<script>
  import { get,post }  from '../../common/function/service';
  export default {
      data() {
		  return {
			params: '',
            styles:[],//年份 价值成长 大小盘 权益暴露
            riskProfitStats:[],
            capabilityExplain:[],
            longholdstats:[],
            interview: '',
            pdfState: false,
		  }
      },
      created() {
        this.params = this.$route.params;
        let code = this.params.code;
        get(`/finance/manager/${code}/styles`).then(res=>{
          if(res.code === 0)this.styles = res.data;
        });
        get(`/finance/manager/${code}/RiskProfitStats`).then(res=>{
          if(res.code === 0)this.riskProfitStats = res.data;
        });
        get(`/finance/manager/${code}/CapabilityExplain`).then(res=>{
          if(res.code === 0)this.capabilityExplain = [
            {name:"股债配置", value:res.data.assetExplain},
            {name:"行业配置", value:res.data.industryExplain},
            {name:"择股能力", value:res.data.stockExplain},
            {name:"交易能力", value:res.data.timingExplain}
          ];
        });
        get(`/finance/manager/${code}/longholdstats`).then(res=>{
          if(res.code === 0)this.longholdstats = res.data;
        });
        get(`/finance/manager/${code}/interview`).then(res=>{
          if(res.code === 0)this.interview = res.data;
        });
      },
      computed: {

      },

      methods: {
        styleFormatter(row, column,cellValue) {
          return cellValue||'暂无更新，可点击生成pdf查看更新';
        },
        riskFormatter(row, column,cellValue) {
          if(cellValue==null)return 'NA';
          return (cellValue*100).toFixed(1) + '%';
        },
        exportPdf(event) {
          this.params = this.$route.params;
          let code = this.params.code;
          get(`/finance/manager/${code}/pdf`).then(res=>{
            if(res.code === 0)this.pdfState = res.data;
            this.$alert('正在为您生成pdf格式的报告，请耐心等候。生成完毕后会发到你的邮箱', '提示', {
                confirmButtonText: 'OK',

            });
          });
        },
      },
      components: {
      },
    }
</script>
<style lang="scss" scoped>
    .managerDetailPage{
        width: 100%;
    }

</style>

